$primary-color: #006a8e;
$text-color: #164d6c;
$bg-light: #f5f9fa;
$border-color: #c3dfe6;

/* CONTENEDOR GENERAL */
.videos-page {
  max-width: 1400px;
  margin: 80px auto;
  padding: 30px;
  background: $bg-light;
  border-radius: 22px;
  box-shadow: 0 6px 20px rgba(0, 0, 0, 0.1);
}

/* TÍTULO CON RAYA */
.title-row {
  display: flex;
  align-items: center;
  margin-bottom: 40px;
}

.title-row .bar {
  font-size: 48px;
  font-weight: 900;
  color: #2c8ead;
  margin-right: 18px;
  line-height: 0.8;
}

.videos-title {
  font-size: 38px;
  font-weight: 800;
  color: $text-color;
  margin: 0;
  text-transform: uppercase;
}

/* FILTRO */
.filter-container {
  display: flex;
  align-items: center;
  gap: 16px;
  margin-bottom: 40px;
  font-size: 18px;

  label {
    font-weight: 600;
    color: $text-color;
  }

  select {
    padding: 12px 16px;
    border-radius: 12px;
    border: 2px solid $border-color;
    font-size: 16px;
    background: #ffffff;
    font-weight: 600;
    cursor: pointer;
    transition: 0.3s ease;

    &:hover {
      border-color: $primary-color;
    }

    &:focus {
      outline: none;
      border-color: $primary-color;
      box-shadow: 0 0 6px rgba(0, 106, 142, 0.3);
    }
  }
}

/* GRID DE VIDEOS */
.video-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
  gap: 35px;
}

/* TARJETA */
.video-card {
  background: #ffffff;
  border-radius: 20px;
  overflow: hidden;
  padding-bottom: 20px;
  box-shadow: 0 6px 20px rgba(0, 0, 0, 0.12);
  transition: transform 0.3s ease, box-shadow 0.3s ease;

  &:hover {
    transform: translateY(-6px);
    box-shadow: 0 12px 28px rgba(0, 0, 0, 0.18);
  }

  video {
    width: 100%;
    height: 260px;
    object-fit: cover;
    border-bottom: 3px solid $border-color;
  }
}

/* TÍTULO DEL VIDEO */
.video-header {
  padding: 20px;

  h3 {
    font-size: 22px;
    font-weight: 700;
    margin: 0;
    color: $primary-color;
  }
}

/* RESPONSIVE */
@media (max-width: 768px) {
  .videos-title {
    font-size: 30px;
  }

  .title-row .bar {
    font-size: 36px;
  }

  video {
    height: 220px !important;
  }

  .filter-container {
    flex-direction: column;
    align-items: flex-start;
  }
}

@media (max-width: 480px) {
  .videos-page {
    padding: 20px;
  }

  .video-grid {
    grid-template-columns: 1fr;
  }

  video {
    height: 200px !important;
  }
}
